import React, { useState, useEffect } from "react";
import fetch from "isomorphic-unfetch";

const Results = ({ newEntry, scoreData, choSpecificHighscores }) => {
  const results = [
    ...scoreData,
    { username: "Jouw naam hier", score: newEntry }
  ].sort((x, y) => x.score - y.score);
  // const choSpecificHighscoresResults = [
  //   ...choSpecificHighscores,
  //   { username: "Jouw naam hier", score: newEntry }
  // ].sort((x, y) => x.score - y.score);

  return (
    <div>
      <h3>Highscores van deze foto:</h3>
      <table>
        <thead>
          <tr>
            <td>Username:</td>
            <td>Score:</td>
          </tr>
        </thead>
        <tbody>
          {choSpecificHighscores.map(
            ({ username, score }) =>
              score && (
                <tr
                  style={{
                    color: username === "Jouw naam hier" ? "red" : "black"
                  }}
                  key={Math.random()}
                >
                  <td>{username}</td>
                  <td>{score}</td>
                </tr>
              )
          )}
        </tbody>
        <style global jsx>
          {`
            body {
              margin: 0;
              padding: 0;
            }
            div {
              width: 100%;
              max-height: 100vh;
              overlay: hidden;
              color: #333;
              font-family: sans-serif;
            }
          `}
        </style>
      </table>
      <table>
        <thead>
          <tr>
            <td>Username:</td>
            <td>Score:</td>
          </tr>
        </thead>
        <tbody>
          {results.map(
            ({ username, score }) =>
              score && (
                <tr
                  style={{
                    color: username === "Jouw naam hier" ? "red" : "black"
                  }}
                  key={Math.random()}
                >
                  <td>{username}</td>
                  <td>{score}</td>
                </tr>
              )
          )}
        </tbody>
      </table>
    </div>
  );
};

export default Results;
